.background-wrapper {
    position: fixed; /* Fixed positioning ensures it stays behind content */
    top: 0;
    left: 0;
    width: 100vw; /* Full viewport width */
    height: 100vh; /* Full viewport height */
    z-index: -1; /* Push the background behind all content */
    overflow: hidden; /* Prevent scrollbars for overflowing elements */
    margin: 0; /* Remove any potential margin causing gaps */
    padding: 0; /* Remove padding */
    box-sizing: border-box; /* Ensure dimensions include padding and borders */
    background: transparent;
  }
  
  .backwrap.gradient {
    width: 100%;
    height: 100%;
    background: transparent;
    /*background: linear-gradient(to bottom right, black, #28282B); */
    /*background: linear-gradient(to bottom right, #141e30, #243b55);*/
    margin: 0; /* Remove any margin */
    padding: 0; /* Remove any padding */
  }


/* Shapes & Animation
*/
.back-shapes {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.back-shapes img {
  opacity: 0.2;
  position: absolute;
  width: 1.5%;
}

.floating {
  position: absolute;
  animation-name: floating;
  -webkit-animation-name: floating;
  animation-duration: 5s;
  -webkit-animation-duration: 5s;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
}
.floating.circle {
  display: inline-block;
  width: 39px;
  height: 39px;
  background-image: url("data:image/png;base64,");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  opacity: 0.2;
}
.floating.square {
  display: inline-block;
  width: 35px;
  height: 35px;
  background-image: url("data:image/png;base64,");
  background-size: cover;
  background-repeat: no-repeat;
