.hero {
    height: 100vh;
    background: url('../assets/images/buildings-2699520.jpg') center center/cover no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    text-align: center;
    padding: 0 20px;
  }
  
  .hero-content h1 {
    font-size: 3rem;
    font-weight: bold;
  }
  
  .cta-button {
    background-color: whitesmoke;
    color: #fff;
    padding: 1rem 2rem;
    border: none;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .cta-button:hover {
    background-color: whitesmoke;
  }
  
  /* Mobile Responsiveness */
  @media (max-width: 768px) {
    .hero-content h1 {
      font-size: 2rem;
    }
  
    .cta-button {
      padding: 0.8rem 1.5rem;
      font-size: 0.9rem;
    }
  }
  